import { request, requestDownload } from 'utils';

// request.prefix = '/api/categories';
const prefix = '/api/students';

export const getStudentsForUnionsMem = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studentCode,
  studentFullName,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/Student/GetUnionStudents',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      studentCode,
      studentFullName,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const updateUnionDate = ({ id, unionDate }) =>
  request({
    prefix,
    url: `/Student/${id}/UpdateUnionDate`,
    params: {},
    data: { ids: [id], unionDate },
    method: 'PUT',
  });

export const exportUnionStudents = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  studentCode,
  studentFullName,
}) =>
  requestDownload({
    defaultFileName: 'fileDownload.xlsx',
    prefix,
    url: `/Student/ExportUnionStudents`,
    method: 'POST',
    data: { trainTypeId, facultyId, courseId, studyFieldId, classId, studentCode, studentFullName },
  });



